<template>
<div>
</div>
</template>
 
<script>
import router from "@/router";
import store from "@/store"; // Vuex    
 
export default {
  created() {
    const token = this.$route.query.token
    if (token) {
      store.commit('setToken', token);
      sessionStorage.setItem('accessToken', token);
      window.alert('');
      router.push({path:'/main'})
    } else {
      window.alert(' .')
      router.push({path:'/intro'})
    }
  }
}
</script>
 
