<template>
    <div class="container mt-4 wrap">
      <div><span class="material-symbols-outlined turn-back" @click="returnMain">undo</span></div>
      <form @submit.prevent="submitPost">
        <div class="mb-3">
          <label for="titleInput" class="form-label"></label>
          <input type="text" class="form-control" id="titleInput" v-model="post.title" maxlength="80" :minlength="minLength" required placeholder=" ">
        </div>
        <div>
          <select v-model="post.category1">
              <option value="chat"></option>
              <option value="game"></option>
              <option value="beauty"></option>
              <option value="study"></option>
              <option value="travel"></option>
          </select>
          <select v-if="post.category1 === 'chat'" v-model="post.category2">
            <option value="chat"></option>
            <option value="common"></option>
            <option value="star"></option>
            <option value="love"></option>
            <option value="food"></option>
          </select>
          <select v-if="post.category1 === 'game'" v-model="post.category2">
            <option value="lol"></option>
            <option value ="overwatch"></option>
            <option value="maplestory"></option>
            <option value="varolant"></option>
            <option value="mabinogi"></option>
          </select>
          <select v-if="post.category1 === 'beauty'" v-model="post.category2">
            <option value="makeup"></option>
            <option value ="fashion"></option>
            <option value="skin"></option>
            <option value="diet"></option>
            <option value="hairstyle"></option>
          </select>
          <select v-if="post.category1 === 'study'" v-model="post.category2">
            <option value="certification"></option>
            <option value ="suneung"></option>
            <option value="toeic"></option>
            <option value="hobby"></option>
            <option value="interview"></option>
          </select>
          <select v-if="post.category1 === 'travel'" v-model="post.category2">
            <option value="oversea"></option>
            <option value ="domestic"></option>
            <option value="festival"></option>
            <option value="event"></option>
          </select>

          <select v-model="post.category3">
            <option value="free"></option>
            <option value ="ask"></option>
            <option value="info"></option>
          </select>

        </div>
        <div class="mb-3">
          <textarea class="form-control contents" id="contentInput" v-model="post.content" maxlength="400" :minlength="minLength" required rows="4" placeholder=" "></textarea>
        </div>
        <div class ="sections">
            <button type="submit" class="btn btn-success">  </button>
            <button type="submit" @click="returnMain" class="btn btn-secondary"></button>
        </div>    
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {

        //img
      postContent: '',
      selectedFile: null,
      imageUrl: null,

        post: {
          title: '',
          content: '',
          id : '',
          category1 :'chat',
          category2 : 'chat',
          category3 : 'free',
        },
        minLength : 5
      };
    },
    methods: {
      returnMain(){
        this.$router.push('/main')
        },
      submitPost() {
        if (this.post.title.length >= this.minLength && this.post.content.length >= this.minLength ) {
            this.PostServe();
        } else {
        alert(` ${this.minLength}   .`);
         }
      },
       PostServe(){
        this.$axios.post('/api/board/post',this.post)
          .then(()=>{
            this.$router.push('/main')
          })
          .catch(error => {
            if (error.response) {
              alert("Error:  .");
            } else if (error.request) {
              alert("Error:   .");
            } else {
              alert(" .");
            }
           });
        },
        handleFileUpload(event){
          this.selectedFile = event.target.files[0];
          console.log('selectedFile:', this.selectedFile);
          this.imageUrl = URL.createObjectURL(this.selectedFile);
          console.log('imageUrl:', this.imageUrl);
    },
        
        },
    mounted() {
        this.post.id = sessionStorage.getItem('userIdx');
    },
    watch: {
    'post.category1'(newValue) {
      if (newValue === 'chat') {
        this.post.category2 = 'chat';
        this.post.category3 = 'free';
      } else if (newValue === 'game') {
        this.post.category2 = 'lol';
        this.post.category3 = 'free';
      } else if (newValue === 'beauty') {
        this.post.category2 = 'makeup';
        this.post.category3 = 'free';
      }else if (newValue === 'study') {
        this.post.category2 = 'toeic';
        this.post.category3 = 'free';
      }else if (newValue === 'travel') {
        this.post.category2 = 'oversea';
        this.post.category3 = 'free';}
  }
}
  }

  </script>
